button{
    border: 1px solid #00af91;
    color: #fff;
    background-color: #00af91;
    margin-left: .5rem;
    width: 5rem;
    height: 5rem;
    padding: 0;
    border-radius: 50%;
    transition: .2s ease-in;
    box-shadow: 0px 3px 1px -2px rgba(0, 0, 0, 0.2), 0px 2px 2px 0px rgba(0, 0, 0, 0.14), 0px 1px 5px 0px rgba(0,0,0,.12);
}