@import url(https://fonts.googleapis.com/css2?family=Ubuntu&display=swap);
.wrapper{display:flex;align-items:center;width:100%;margin:0;padding:0;background:#fcf8e8;height:100vh}.wrapper .ui{position:relative;margin:auto;background:#ffffff;width:19rem;min-height:30rem;display:flex;flex-direction:column;box-shadow:0 2.8px 2.2px #00000009, 0 6.7px 5.3px #0000000c, 0 12.5px 10px #0000000f, 0 22.3px 17.9px #00000012, 0 41.8px 33.4px #00000016, 0 100px 80px #0000001f}.wrapper .ui .input{position:absolute;bottom:3rem;display:flex;width:100%;justify-content:center;transform:translateY(-50%)}.wrapper .ui .footer{transform:translateY(50%);position:absolute;bottom:0;width:100%;display:flex;justify-content:center}.date{display:flex;margin:1rem;padding:0.1rem;margin-top:1.4rem;margin-bottom:0;text-align:center;align-items:baseline;justify-content:space-between}.date .left{display:flex;align-items:center}.date .day{margin-right:.6rem;font-size:2.1rem;font-weight:bold}.date .weekday{margin-top:0.7rem;font-size:1rem;font-weight:bold}.date .month-year{display:flex;flex-direction:column}.date .month-year .month{font-weight:bold}.cardListWrapper{display:flex;justify-content:center;height:100%;margin:1rem;padding-bottom:6rem}.cardListWrapper .cardsWrapper{width:100%}.card{display:flex;height:3rem;align-items:center;justify-content:space-between;padding:4px}.card span{display:block;width:15rem;word-wrap:break-word}.card .done-task{text-decoration:line-through}.card .buttons{text-align:end}.card .buttons button.done{border:1px solid #00af91;color:#00af91;background-color:#fff;margin-left:.5rem;width:20px;height:20px;padding:0;border-radius:50%;transition:.2s ease-in}.card .buttons button.done:hover{color:#fff;background:#00af91}.card .buttons button.delete{border:1px solid #c70039;color:#c70039;background-color:#fff;margin-left:.5rem;width:20px;height:20px;padding:0;border-radius:50%;transition:.2s ease-in}.card .buttons button.delete:hover{color:#fff;background:#c70039}.card .task.outlined{text-decoration:line-through}button{border:1px solid #00af91;color:#fff;background-color:#00af91;margin-left:.5rem;width:5rem;height:5rem;padding:0;border-radius:50%;transition:.2s ease-in;box-shadow:0px 3px 1px -2px rgba(0,0,0,0.2),0px 2px 2px 0px rgba(0,0,0,0.14),0px 1px 5px 0px rgba(0,0,0,0.12)}body{margin:0%}*{color:#557174;font-family:'Ubuntu', sans-serif}

